<template>
  <div class="home-box">
      <div class="header">
        <div class="detail">{{greet}}<span class="count-name">{{AccountData.nickName}}</span></div>
        <div class="search">
            <van-search v-model="value" shape="round" placeholder="输入商品名称" @search="search(value)" @input="isShow(value)"/>
        </div>
      </div>
      <div class="h-content" v-if="isOpen == 0">
        <div class="h-banner">
          <van-swipe class="my-swipe" :width="355" :autoplay="2000" indicator-color="tomato">
            <van-swipe-item v-for="(item,index) in images" :key="index">
                  <img :src="item.bannerImg"  id="img"/>
            </van-swipe-item>
          </van-swipe>
        </div>
        <div class="h-title">
          <div class="ishot-box">热卖推荐</div>
        </div>
        <div class="h-item">
          <div class="hot-items" v-for="(item,index) in hotType" :key="index" @click="toDetail(item.pid)">
               <Product :pro="item">
                  <template #hot>
                     <div class="mask">hot</div>
                  </template>
               </Product>
          </div>
        </div>
      </div>
      <div class="search-box" v-if="isOpen == 1">
        <div class="search-content">
            <div class="search-item" v-for="(item,index) in AllSearchData" :key="index" @click="toDetail(item.pid)">
              <Product :pro="item"/>

            </div>
        </div>
      </div>
      <div class="search-box" v-if="isOpen == 2">
            <van-empty description="没有找到相关商品!" />
      </div>
  </div>
</template>

<script>
import Product from '../components/Product.vue'
export default {
  name: 'Home',
  components: {
    Product
  },
  data(){
    return{
      value:"",
      images: [],
      hotType: [],
      isOpen:0,
      AllSearchData:[],
      greet:'',
      AccountData:[]
    }
  },
  // 立即执行
  created(){
      this.getBannerData();
      this.getHotType();
      this.getTime();
      this.findAccountInfo();
  },
  methods:{
    getTime(){
        // let date = new Date("2021-2-3 8:03:04");
        let date = new Date();
        let hours = date.getHours();
        if(hours >0 && hours<6){  
            this.greet = "晚上好";
        }else if(hours >=6 && hours<12){
          this.greet = "上午好";
        }else if(hours >=12 && hours<18){
          this.greet = "下午好";
        }else{
          this.greet = "晚上好";
        }
    },
     // 个人资料
    findAccountInfo(){
        //获取token
        let tokenString = this.$cookies.get("TOKEN");

        this.axios({
            method: "GET",
            url: "/findAccountInfo",
            params: {
            tokenString,
            },
        })
            .then((res) => {
            console.log("findAccountInfo ===>",res);
                if(res.data.code == "B001"){
                    this.AccountData = res.data.result[0];
                    console.log(this.AccountData.nickName);
                    
                }
            })
            .catch((err) => {
                console.log(err);
            });
                },
    // 输入框发生改变的时候
    isShow(val){
        if(val.length == 0){
          // this.isOpen = true;
          this.isOpen=0;
        }
    },
    toDetail(pid){
        this.$router.push({name: "Detail",params: {pid}});
    },
    // 搜索商品
    search(sc){
        this.isOpen = false;
        this.axios({
        method: "GET",
        url: "/search",
        params: {
          name:sc
        }
      })
        .then((res) => {
          console.log("getSelectionData res==>",res);
          if (res.data.code === "Q001" && res.data.result.length !=0) {
            this.AllSearchData = res.data.result;
            this.isOpen = 1;
          }else if(res.data.code === "Q001" && res.data.result.length == 0){
            this.isOpen = 2;
            // Boolean(undefined) ==>false
          }
        
        })
        .catch((err) => {
            console.log(err);
        });
    },
    // 跳转详情页面
    toDetail(pid){
        this.$router.push({name: 'Detail', params: {pid}});
    },
      getBannerData(){
        this.axios({
          method: "GET",
          url: "/banner",
          params: {}
        })
        .then((res) => {
          
          // 
          if(res.data.code === 300){
              this.images = res.data.result;
              // 
          }
        })
        .catch((err) => {
          
        });
      },
      getHotType(){
          this.axios({
            method: "GET",
            url: "/typeProducts",
            params: {
              key: 'isHot',
              value:1
            }
          })
          .then((res) => {
            
            
            if(res.data.code === 500){
                this.hotType = res.data.result;
                // 
            }
          })
          .catch((err) => {
            
          });
      }
  }
  
}
</script>

